<template>
  <div>
    <el-form :inline="true" class="demo-form-inline" style="margin-top: 20px">
      <el-row style="margin-left: -200px">
        <el-form-item label="员工姓名">
          <el-input size="mini" style="width: 200px" v-model="searchCondition.careworkerName"></el-input>
        </el-form-item>
        <el-form-item label="科室">
          <el-input size="mini" style="width: 200px" v-model="curOrgName" @focus="orgSearchDialog">
            <i slot="suffix" class="el-input__icon el-icon-circle-close" @click="clearOrgInput"></i>
          </el-input>
        </el-form-item>
        <el-form-item label="时间间隔">
          <div class="block">
            <el-date-picker
              v-model="selectedDate"
              type="daterange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd"
              size="mini"
              style="width:200px"
            >
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button size="mini" icon="el-icon-search" type="primary" @click="searchDutyInfoList">查 询</el-button>
        </el-form-item>
      </el-row>
      <el-row>
        <div>
          <el-table
            highlight-current-row
            :data="tableData"
            border
            style="width: 100%"
            :v-loading="tableLoading"
            stripe
          >
            <el-table-column
              prop="careworkerId"
              v-if="false"
            >
            </el-table-column>
            <el-table-column
              prop="careworkerName"
              fixed
              align="center"
              label="员工姓名"
              width="150"
            >
            </el-table-column>
            <template v-for="(col ,index) in cols">
              <el-table-column v-if="col.type==='normal'" :prop="col.prop" :label="col.label" width="200"
                               align="center">
                <template slot-scope="scope">
                  <el-col v-if="scope.row[scope.column.property] != null">

                    <el-row v-for="row in scope.row[scope.column.property].info" :key="row">
                      {{ row }}
                    </el-row>
                    <el-row v-if="scope.row[scope.column.property].signIn">
                      <el-row style="color: green;">
                        已签到
                      </el-row>
                      <el-button @click="signIn(scope,false)" size="mini">
                        取消签到
                      </el-button>
                    </el-row>
                    <el-row v-else>
                      <el-row style="color: red;">
                        未签到
                      </el-row>
                      <el-button @click="signIn(scope,true)" size="mini">
                        签到
                      </el-button>
                    </el-row>
                  </el-col>
                  <el-col v-else>
                    <el-row>
                      没有排班
                    </el-row>
                  </el-col>

                </template>
              </el-table-column>
            </template>
          </el-table>
          <el-pagination
            @current-change="searchDutyInfoList"
            :current-page.sync="searchCondition.curPage"
            :page-size="searchCondition.pageSize"
            layout="prev, pager, next, jumper"
            :total="searchCondition.totalSize">
          </el-pagination>
        </div>
      </el-row>
    </el-form>

    <el-form ref="OrgForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog title="科室参照" style="padding: 0px;" :close-on-click-modal="false"
                   :visible.sync="showOrgSearchDialog" width="30%">
          <el-row>
            <el-tree
              :props="treeData"
              :load="loadChild"
              node-key="id"
              lazy
              show-checkbox
              ref="tree"
              @check="orgNodeClick"
            >
            </el-tree>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="showOrgSearchDialog = false">取 消</el-button>
              <el-button size="mini" type="primary" @click="orgClick">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>
  </div>
</template>

<script>
import {formatMonthAndDay} from '../../utils/utils'

export default {
  data () {
    return {
      selectedDate: '',
      cols: [],
      tableData: [
      ],
      tableLoading: false,
      treeData: {
        children: 'children',
        label: 'label',
        id: 'id',
        isLeaf: 'isLeaf',
        disabled: 'disabled'
      },
      curOrgName: '',

      searchCondition: {
        careworkerName: null,
        startDate: null,
        endDate: null,
        orgId: null,
        curPage: 1,
        pageSize: 100,
        totalSize: 10
      },
      showOrgSearchDialog: false

    }
  },
  mounted: function () {

  },

  methods: {
    signIn (data, status) {
      let target = data
      this.putRequest('/busi/routine', {
        careworkerId: data.row.careworkerId,
        date: data.column.property,
        isWorked: status
      }).then(resp => {
        console.log(resp)
        if (!(resp && resp.data.code === '200')) {
          this.$message.error({
            type: 'fail',
            message: resp.data.msg
          })
        } else {
          target.row[target.column.property].signIn = status
          this.$message({
            type: 'success',
            message: resp.data.msg
          })
        }
      })
    },

    orgSearchDialog () {
      this.showOrgSearchDialog = true
    },
    loadChild (node, resolve) {
      let id = (node.parent == null ? null : node.data.id)
      let resolve1 = resolve
      this.tableLoading = true
      this.postRequestJson('/busi/baseinfo/organization/list', {
        orgId: id,
        projectId: 2
      }).then(resp => {
        this.tableLoading = false
        if (resp && resp.status === 200) {
          const tdata = []
          let result = resp.data.msg.result

          for (let i = 0; i < result.length; i++) {
            tdata.push({
              id: resp.data.msg.result[i].id,
              label: resp.data.msg.result[i].name,
              children: [],
              isLeaf: resp.data.msg.result[i].isLeaf,
              disabled: !resp.data.msg.result[i].isLeaf
            })
          }
          resolve1(tdata)
        }
      })
    },
    orgNodeClick (data, nodes) {
      let target = []
      target.push(data)
      this.$refs.tree.setCheckedNodes(target, true)
    },
    orgClick () {
      let data = this.$refs.tree.getCheckedNodes()
      if (data.length > 0) {
        this.curOrgName = data[0].label
        this.searchCondition.orgId = data[0].id
      }
      this.showOrgSearchDialog = false
    },
    clearOrgInput () {
      this.curOrgName = ''
      this.searchCondition.orgId = null
    },

    searchDutyInfoList () {
      this.tableLoading = true
      this.searchCondition.startDate = this.searchCondition.endDate = null
      this.cols = []
      this.tableData = []

      let tmp = setTableHeader(this.selectedDate, this)

      if (tmp == null) {
        this.$message.error({
          type: 'fail',
          message: '时间间隔不得超过一个月'
        })
        return
      } else {
        this.searchCondition.startDate = tmp.start
        this.searchCondition.endDate = tmp.end
      }

      this.postRequestJson('/busi/routine/list', this.searchCondition).then(resp => {
        this.tableLoading = false
        if (!(resp && resp.data.code === '200')) {
          this.$message.error({
            type: 'fail',
            message: resp.data.msg
          })
          return
        }
        console.log(resp.data.msg.result)
        let routines = resp.data.msg.result
        for (let i = 0; i < routines.length; i++) {
          let routineInfoDTO = routines[i].routineInfoDTO

          let cell = {
            careworkerId: routines[i].careworkerId,
            careworkerName: routines[i].careworkerName
          }

          for (let j = 0; j < routineInfoDTO.length; j++) {
            if (cell[routineInfoDTO[j].date] == null) {
              cell[routineInfoDTO[j].date] = {
                info: [],
                signIn: true
              }
            }
            cell[routineInfoDTO[j].date].info.push(getRoutineString(routineInfoDTO[j]))
            cell[routineInfoDTO[j].date].signIn = cell[routineInfoDTO[j].date].signIn && routineInfoDTO[j].isWorked
          }
          console.log(cell)

          // cell[routineInfoDTO[0].date] = "<el-row >"+getRoutineString(routineInfoDTO[0])+"</el-row></br>";
          // if(routineInfoDTO[0].isWorked){
          //   isWorked[routineInfoDTO[0].date] = '<el-row style="color: green;">'+"已签到"+'</el-row></br>'
          // }else{
          //   isWorked[routineInfoDTO[0].date] = '<el-row style="color: red;">'+"未签到"+'</el-row></br>'
          // }
          // for(let j = 1;j<routineInfoDTO.length;j++){
          //   if(cell[routineInfoDTO[j].date] != '' && cell[routineInfoDTO[j].date] != null){
          //     cell[routineInfoDTO[j].date] = cell[routineInfoDTO[j].date]+"<el-row >"+getRoutineString(routineInfoDTO[j])+"</el-row></br>";
          //   }else{
          //     cell[routineInfoDTO[j].date] = "<el-row >"+getRoutineString(routineInfoDTO[j])+"</el-row></br>";
          //     if(routineInfoDTO[j].isWorked){
          //       isWorked[routineInfoDTO[j].date] = '<el-row style="color: green;">'+"已签到"+'</el-row></br>'
          //     }else{
          //       isWorked[routineInfoDTO[j].date] = '<el-row style="color: red;">'+"未签到"+'</el-row></br>'
          //     }
          //   }
          // }
          // for(let key in isWorked){
          //     cell[key] = cell[key] + isWorked[key];
          // }

          this.tableData.push(cell)
        }
      })
    }

  }

}

function setTableHeader (selectedDate, header) {
  let diff = 0
  let start = ''
  let end = ''

  if (selectedDate != null && selectedDate != '' && selectedDate.length > 0) {
    start = selectedDate[0]
    end = selectedDate[1]
    let b = new Date(start)
    let e = new Date(end)
    diff = Math.floor((e.getTime() - b.getTime()) / (24 * 3600 * 1000)) + 1
  } else {
    let date = new Date()
    let year = date.getFullYear()
    let month = date.getMonth() + 1
    diff = (new Date(year, month, 0)).getDate()
    date.setDate(1)
    start = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate()
    date.setDate(diff)
    end = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate()
  }
  if (diff > 31) return null

  let begin = new Date(start)
  for (let i = 0; i < diff; i++) {
    let tmp = getDateString(begin)
    header.cols.push({
      label: tmp,
      prop: tmp,
      type: 'normal'
    })
    begin.setDate(begin.getDate() + 1)
  }
  return {start, end}
}

function getDateString (date) {
  return date.getFullYear() + '-' + formatMonthAndDay((date.getMonth() + 1) + '') + '-' + formatMonthAndDay(date.getDate() + '')
}

function getRoutineString (routine) {
  if (routine == null || routine == '') return
  let target = routine.schedule + '-' + routine.property + '-' + routine.dayIndex + '天'
  return target
}
</script>

<style scoped>
  .divWorkClass {
    width: 500px;
    height: 200px;
    border: 1px solid #4040FB;
    border-radius: 5px;
  }

  .tb-edit .el-input {
    display: none;
  }

  .tb-edit .current-row .el-input {
    display: block;
  }

  .tb-edit .current-row .el-input + span {
    display: none;
  }
</style>



