<template>

  <div style="margin-top: 20px">
    <el-form>
      <el-row>
        <el-table
          :data="resourceList"
          v-loading="tableLoading"
          border
          size="mini"
          style="width: 100%">
          <el-table-column
            prop="faid"
            align="center"
            label="父记录号">
          </el-table-column>
          <el-table-column
            prop="id"
            v-if="false"
            align="center">
          </el-table-column>
          <el-table-column
            prop="name"
            align="center"
            label="资源名">
          </el-table-column>
          <el-table-column
            prop="url"
            align="center"
            label="URL">
          </el-table-column>
          <el-table-column
            prop="type"
            align="center"
            label="类型">
          </el-table-column>
          <el-table-column
            prop="componet"
            align="center"
            label="资源名称">
          </el-table-column>
          <el-table-column
            prop="icon"
            align="center"
            label="图标">
          </el-table-column>
          <el-table-column
            prop="scope"
            align="center"
            label="范围">
          </el-table-column>
          <el-table-column
            prop="requesmethod"
            align="center"
            label="资源请求方法">
          </el-table-column>
          <el-table-column
            prop="iSDisabled"
            align="center"
            label="是否不可用">
          </el-table-column>
          <el-table-column
            prop="isRequireAuth"
            align="center"
            label="是否需要权限">
          </el-table-column>
          <el-table-column
            prop="version"
            align="center"
            label="版本">
          </el-table-column>
          <el-table-column
            align="center"
            label="操作">
            <template slot-scope="scope">
              <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">编辑
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </el-form>

    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="resourceVisible"  width="65%">
          <el-row>
            <el-col :span="6">
              <el-form-item label="资源名:" >
                <el-input prefix-icon="el-icon-edit"   size="mini" style="width: 150px"  placeholder="请输入资源名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="URL:" prop="startCode">
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder="请输入URL"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="类型:" prop="endCode">
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder="请输入类型"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="资源名称:" prop="endCode">
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder="请输入资源名称"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="图 标:" >
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 155px"  placeholder="请输入图标"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="资源操作域:" prop="startCode">
                <el-input prefix-icon="el-icon-edit"   size="mini" style="width: 110px"  placeholder="请输入资源操作域"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="资源请求方法:" prop="endCode">
                <el-input prefix-icon="el-icon-edit"   size="mini" style="width: 95px"  placeholder="请输入资源请求方法"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="是否不可用:" prop="endCode">
                <el-select  v-model="disabledid" size="mini"  placeholder="是否需要权限" style="width:135px">
                  <el-option  v-for="item in disabledData" :key="item.disabledid" :label="item.mame" :value="item.disabledid"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="是否需要权限:" >
                <el-select  v-model="requireid" size="mini"  placeholder="是否需要权限" style="width:100px">
                  <el-option  v-for="item in requireData" :key="item.requireid" :label="item.mame" :value="item.requireid"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="版本:" >
                <el-input prefix-icon="el-icon-edit"    size="mini" style="width: 150px"  placeholder="请输入版本"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="13">
              <el-form-item style="float: right">
                <el-button size="mini" icon="el-icon-edit"  type="primary" @click="madyifResource">修改资源</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-dialog>
      </div>
    </el-form>
  </div>




</template>

<script>
  export default {
    data() {
      return {
        resourceList:[],
        tableLoading:false,
        dialogTitle:'',
        resourceVisible:false,
        disabledid:'1',
        requireid:'1',
        requireData:[
          {
            requireid:'1',
            mame:'是'
          },{
            requireid:'0',
            mame:'否'
          }
        ],
        disabledData:[          {
          disabledid:'1',
          mame:'是'
        },{
          disabledid:'0',
          mame:'否'
        }
        ],
      };
    },
    mounted: function () {

      this.resourceVisible=true;

    },
    methods: {
      showEditEmpView(row){
        this.resourceVisible=true;
      },
      madyifResource(){
        this.dialogTitle="编辑资源信息";
        this.resourceVisible=false;
      },
    }
  }
</script>

<style scoped>

</style>



