<template>

  <div>
    <el-form :inline="true" class="demo-form-inline" style="margin-top: 10px">
      <el-row>
        <el-form-item label="病人姓名:" style="float: left" prop="username">
          <el-input prefix-icon="el-icon-edit" v-model="patientName" size="mini" style="width: 150px"
                    placeholder="请输入病人姓名"></el-input>
        </el-form-item>
        <el-form-item label="派工状态:" style="float: left" prop="username">
          <el-select v-model="disData.isDisabled" size="mini" placeholder="派工状态" style="width:150px">
            <el-option v-for="item in disData" :key="item.isDisabled" :label="item.name"
                       :value="item.isDisabled"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="float: left">
          <el-button size="mini" icon="el-icon-search" type="primary" @click="searchMission">查询</el-button>
        </el-form-item>
      </el-row>
      <el-row>
        <el-table
          :data="missionList"
          border
          v-loading="tableLoading"
          stripe
          size="mini"
          style="width: 100%">
          <el-table-column
            prop="patientName"
            align="center"
            fixed
            label="病人姓名">
          </el-table-column>
          <el-table-column
            prop="patientCode"
            align="center"
            fixed
            label="病人编号">
          </el-table-column>
          <el-table-column
            prop="orgName"
            align="center"
            label="现在科室">
          </el-table-column>
          <el-table-column
            prop="requestCode"
            align="center"
            v-if="hideRow"
            label="服务编号">
          </el-table-column>
          <el-table-column
            prop="hospitalizedDate"
            label="入院时间"
            align="center">
          </el-table-column>
          <el-table-column
            prop="serviceDate"
            label="服务开始时间"
            align="center">
          </el-table-column>
          <el-table-column
            prop="serviceName"
            label="服务名称"
            align="center">
          </el-table-column>
          <el-table-column
            prop="serviceTai"
            label="服务状态"
            align="center">
          </el-table-column>
          <el-table-column
            prop="serviceStatus"
            v-if="false"
          >
          </el-table-column>
          <el-table-column
            prop="serviceType"
            label="服务类型"
            align="center">
          </el-table-column>
          <el-table-column
            prop="price"
            label="服务单价"
            align="center">
          </el-table-column>
          <el-table-column
            fixed="right"
            label="操作"
            align="center">
            <template slot-scope="scope">
              <el-button style="padding: 3px 10px 3px 10px;margin: 2px" @click="showDispatched(scope.row)" size="small"
                         v-if="scope.row.serviceStatus">派遣操作
              </el-button>
              <el-button style="padding: 3px 10px 3px 10px;margin: 2px" @click="CloseDispatched(scope.row)" size="small"
                         v-if="scope.row.serviceStatus">取消派遣
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="searchMission"
          :current-page.sync="currentPage"
          :page-size="curPageSize"
          layout="prev, pager, next, jumper"
          :total="totalSize">
        </el-pagination>
      </el-row>
    </el-form>

    <!-- 派遣界面-->
    <el-form ref="DispatchedForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog :title="DdialogTitle" style="padding: 10px;" :close-on-click-modal="false"
                   :visible.sync="DdialogVisible" width="60%">
          <el-row>
            <el-form-item label="已派遣护工" style="margin-top: -30px">
              <el-table
                border
                :data="DList"
                v-loading="tableLoading"
                stripe
                height="120"
                size="mini"
                style="width: 100%;">
                <el-table-column
                  prop="careworkerCode"
                  align="center"
                  fixed
                  label="护工编号">
                </el-table-column>
                <el-table-column
                  prop="careworkerName"
                  align="center"
                  label="护工名称">
                </el-table-column>
                <el-table-column
                  prop="missionDate"
                  label="派遣时间"
                  align="center">
                </el-table-column>
              </el-table>
            </el-form-item>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="护工姓名:">
                <el-input prefix-icon="el-icon-edit" v-model="carreName" size="mini" style="width: 150px"
                          placeholder="请输入护工姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item>
                <el-button size="mini" type="primary" @click="getCareWork">查询</el-button>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-form-item label="未派遣护工" style="margin-top: -30px">
              <el-table
                border
                :data="DispatchedList"
                v-loading="tableLoading"
                @selection-change="SelectionChange"
                stripe
                height="200"
                size="mini"
                style="width: 100%;height: 200px;">
                <el-table-column
                  type="selection"
                  align="left"
                  width="30">
                </el-table-column>
                <el-table-column
                  prop="careworkerId"
                  v-if="hideRow"
                  label="陪护人员记录号"
                  align="center"
                >
                </el-table-column>
                <el-table-column
                  prop="code"
                  align="center"
                  fixed
                  label="护工编号">
                </el-table-column>
                <el-table-column
                  prop="name"
                  align="center"
                  label="护工名称">
                </el-table-column>
                <el-table-column
                  prop="orgName"
                  align="center"
                  label="所属科室">
                </el-table-column>
                <el-table-column
                  prop="startDateTime"
                  label="入职时间"
                  align="center">
                </el-table-column>
                <el-table-column
                  prop="endDateTime"
                  label="离职时间"
                  align="center">
                </el-table-column>
              </el-table>
            </el-form-item>
            <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
              <el-pagination
                background
                :page-size="10"
                :current-page="CareWorkcurrentPage"
                @current-change="getCareWorkChean"
                layout="prev, pager, next"
                :total="CareWorktotalCount">
              </el-pagination>
            </div>
          </el-row>
          <el-row>
            <el-col :span="10">
              <el-form-item label="执行派工时间:" prop="username">
                <div class="block">
                  <el-date-picker v-model="DDateTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" size="mini"
                                  placeholder="选择日期" style="width: 180px">
                  </el-date-picker>
                </div>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item>
                <el-button size="mini" type="primary" @click="missionClick">执行派工</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-dialog>
      </div>
    </el-form>

  </div>

</template>

<script>
  export default {
    data() {
      return {
        missionList: [],
        carreName: '',
        patientName: '',
        hideRow: false,
        tableLoading: false,
        value1: '',
        totalSize: 0,
        totalCount: -1,
        CareWorktotalCount: -1,
        currentPage: 1,
        CareWorkcurrentPage: 1,
        curPageSize: 10,
        disData: [{
          isDisabled: '1',
          name: '待派遣'
        }, {
          isDisabled: '0',
          name: '全部'
        }],

        DdialogTitle: '',
        DdialogVisible: false,
        DispatchedList: [],
        tableLoading: false,
        resourceSelection: [],
        DRequestCode: '',
        DDateTime: '',
        DList: [],

      }


    },
    mounted: function () {
      this.searchMission();
    },
    methods: {
      searchMission() {
        this.tableLoading=true;
        this.missionList = [];
        this.postRequestJson('/busi/mission/list', {
          name: this.patientName,
          projectId: this.$store.state.user.projectId,
          isDisabled: this.disData.isDisabled === "1",
          curPage: this.currentPage,
          pageSize: this.curPageSize
        }).then(resp => {
          this.tableLoading=false;
          if (!(resp && resp.data.code === '200')) {
            if (resp.data.code === "200") {
              this.$message({
                type: 'fail',
                message: resp.data.msg
              });
              return;
            } else {
              this.$message({
                type: 'fail',
                message: resp
              });
              return;
            }
          }
          let result = resp.data.msg;
          this.totalSize = result.totalSize;

          let missions = result.result;
          let serviceTai = '';
          console.log(missions);
          for (let i = 0; i < missions.length; i++) {
            let status = true;
            if (missions[i].missionDate == null && missions[i].requestEndDate == null) {
              serviceTai = "待派遣";
            } else if (missions[i].missionDate != null && missions[i].requestEndDate == null) {
              serviceTai = "服务中";
            } else if (missions[i].requestEndDate != null) {
              serviceTai = "已结束";
              status = false;
            }
            this.missionList.push({
              patientName: missions[i].patientName,
              patientCode: missions[i].patientCode,
              orgName: missions[i].orgName,
              /*  orderCode:missions[i].orderCode,*/
              requestCode: missions[i].requestCode,
              hospitalizedDate: missions[i].hospitalizedDate,
              serviceDate: missions[i].requestStartDate,
              serviceType: missions[i].careTypePorperty,
              price: missions[i].careTypePrice,
              serviceName: missions[i].careTypeName,
              serviceTai: serviceTai,
              serviceStatus: status
            });
          }
        });
      },

      SelectionChange(val) {
        this.resourceSelection = val;
      },
      //打开派遣弹窗
      showDispatched(row) {
        this.DdialogTitle = "派遣任务";
        this.DdialogVisible = true;
        this.DRequestCode = row.requestCode;
        this.getCareWork();
        this.getCareWorkByRecode(row.requestCode);
      },

      CloseDispatched(row) {
        this.deleteRequest('/busi/mission?requestCode=' + row.requestCode).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMsage(resp.data.msg);
          } else {
            this.getMsage(resp.status);
          }
        })
      },

      //查询已派遣的护工
      getCareWorkByRecode(requestCode) {
        this.postRequestJson('/busi/mission/detail', {
          requestCode: requestCode,
        }).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if (resp.data.code === "200") {
              this.DList = resp.data.msg.result;

            } else {
              this.getMsage(resp.data.msg);
            }
          } else {
            this.getMsage(resp.status)
          }
        })

      },

      //全部护工分页
      getCareWorkChean(getCareWorkChean) {
        this.CareWorkcurrentPage = getCareWorkChean;
        this.getCareWork();
      },

      //查询全部护工
      getCareWork() {
        this.tableLoading = true;
        this.postRequestJson('/busi/careworker/list', {
          name: this.carreName,
          curPage: this.CareWorkcurrentPage,
          pageSize: 10
        }).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if (resp.data.code === "200") {
              this.DispatchedList = resp.data.msg.result;
              this.CareWorktotalCount = resp.data.msg.totalSize;
            } else {
              this.getMsage(resp.data.msg);
            }
          } else {
            this.getMsage(resp.status);
          }
        })
      },

      //执行派工
      missionClick() {
        if (this.DDateTime === '') {
          this.getMsage("派遣时间不能为空");
          return
        }
        if (this.resourceSelection.length === 0) {
          this.getMsage("请选择派遣护工，注意：已派遣的护工不能派遣");
          return
        }
        const careworkerIds = [];
        for (let i = 0; i < this.resourceSelection.length; i++) {
          careworkerIds.push(this.resourceSelection[i].careworkerId)
        }
        this.postRequestJson('/busi/mission', {
          careworkerIds: careworkerIds,
          requestCode: this.DRequestCode,
          dateTime: this.DDateTime,
          clerk: this.user.name
        }).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if (resp.data.code === "200") {
              this.getMsage("派遣成功");
              this.DdialogVisible = false;
            } else {
              this.getMsage(resp.data.msg);
            }
          } else {
            this.getMsage(resp.status);
          }
        })
      },
      //信息提示框
      getMsage(data) {
        this.$confirm(data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.searchMission();
        }).catch(() => {
        });
      },


    },
    computed: {
      user() {
        return this.$store.state.user;
      },
    }


  }


</script>

<style scoped>

</style>



