<template>
  <div style="margin-top: 20px">
    <el-form>
      <el-row>
        <el-col :span="6">
          <el-form-item label="日期:" >
            <div class="block">
              <el-date-picker
                v-model="dateWork"
                type="date"
                value-format="yyyy-MM-dd"
                size="mini"
                placeholder="选择日期">
              </el-date-picker>
            </div>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="制定的班次:" >
            <el-select  size="mini"  v-model="schedule" placeholder="制定的班次" >
              <el-option  v-for="item in gradeData" :key="item.grade" :label="item.mame" :value="item.grade"> </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="14">
          <template >
            <el-transfer  v-model="careData" :data="dataList":titles="temptit" ></el-transfer>
          </template>
        </el-col>
      </el-row>
      <el-row>

        <el-col :span="14" style="margin-top:30px">
          <el-button size="mini"  icon="el-icon-circle-plus" type="primary" @click="submitOnclik" >保存设置
          </el-button>
        </el-col>

      </el-row>


    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        dialogVisible:false,
        temptit:["待排班","已选中"],
        dateWork:"",
        dialogTitle:'',
        schedule:'1',
        careData:[],
        dataList:[],
        gradeData:[
          {
            grade:'1',
            mame:'白班'
          },{
            grade:'2',
            mame:'夜班'
          }
        ],
      };
    },
    mounted: function () {
      this.getDataList();
    },
    methods: {

        getDataList(){
          this.postRequestJson('/busi/careworker/list',{
            name:'',
            curPage:1,
            pageSize:1000,
            isOnWork:true,
            projectId:this.$store.state.user.projectId,
          }).then(resp=> {
            this.tableLoading = false;
            if (resp && resp.status === 200) {
              console.log(resp.data.msg.result);
              const data = [];
              for (let i = 0; i <resp.data.msg.result.length; i++) {
                data.push({
                  key: resp.data.msg.result[i].careworkerId,
                  label: resp.data.msg.result[i].name,
                });
              }
              this.dataList=data;
            }
          });
      },

      //保存设置
      submitOnclik(){
       this.postRequestJson('/busi/routine',{
          date: this.dateWork,
          dayIndex:1,
          schedule:this.schedule,
          property:1,
          careworkerIds:this.careData
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
              this.getMasgg(resp.data.msg);
          }
        })
      },

      //消息提示
      getMasgg(data){
        this.$confirm(data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.dialogVisible=false;
        }).catch(() => {
        });
      },


    }
  };
</script>
<style>
  .el-transfer-panel__item .el-checkbox__input {
    position: absolute;
    top: 8px;
    margin-left: -25px;
  }
</style>



