<template>
  <div style="margin-top: 20px">
    <el-form >
      <el-row>
        <el-col :span="6">
          <el-form-item label="申请类型:" prop="endCode">
            <el-select  size="mini"  v-model="property" placeholder="申请类型" style="width:150px">
              <el-option  v-for="item in typeData" :key="item.property" :label="item.mame" :value="item.property"> </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="申请时间:" prop="startCode">
            <div class="block">
              <el-date-picker   v-model="date" value-format="yyyy-MM-dd" type="date"  style="width: 155px" size="mini" placeholder="选择日期">
              </el-date-picker>
            </div>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="申请班次:" prop="endCode">
            <el-select  size="mini"  v-model="schedule" placeholder="申请班次" style="width:150px">
              <el-option  v-for="item in gradeData" :key="item.schedule" :label="item.mame" :value="item.schedule"> </el-option>

            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="护工姓名:" >
            <el-input prefix-icon="el-icon-edit"  v-model="CareWorkName" @focus="onclick"  size="mini" style="width: 150px"  placeholder="请输入陪护类型名称"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-form-item label="时间长度:" prop="endCode">
            <el-input prefix-icon="el-icon-edit" v-model="dayIndex"  size="mini" style="width: 150px"  placeholder="请输入时长"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item >
            <el-button size="mini" icon="el-icon-plus"  type="primary" @click="subminOnclick">提交申请</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

    <el-form  ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible"  width="65%">
          <el-row>
            <el-table
              :data="careWorkerList"
              v-loading="tableLoading"
              border
              highlight-current-row
              @current-change="SelectionChange"
              size="mini"
              style="width: 100%">
              <el-table-column
                prop="code"
                align="center"
                fixed
                label="员工编号">
              </el-table-column>
              <el-table-column
                prop="careworkerId"
                v-if="false"
                label="陪护人员记录号"
                align="center">
              </el-table-column>
              <el-table-column
                prop="name"
                align="center"
                label="员工姓名">
              </el-table-column>
              <el-table-column
                prop="orgName"
                align="center"
                label="所在科室">
              </el-table-column>
              <el-table-column
                prop="startDateTime"
                align="center"
                label="入职时间">
              </el-table-column>
              <el-table-column
                prop="endDateTime"
                align="center"
                label="离职时间">
              </el-table-column>
            </el-table>
            <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
              <el-pagination
                background
                :page-size="10"
                :current-page="currentPage"
                @current-change="currentChange"
                layout="prev, pager, next"
                :total="totalCount">
              </el-pagination>
            </div>

          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="dialogVisible=false">取 消</el-button>
              <el-button size="mini" type="primary" @click="onClickEmp">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>

  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        careWorkerList: [],
        dialogVisible: false,
        dialogTitle:'',
        schedule:'1',
        dayIndex:'',
        property:'2',
        date:'',
        totalCount: -1,
        CareWorkName:'',
        currentPage:1,
        resourceSelection:[],
        tableLoading: false,
        gradeData:[
          {
            schedule:'1',
            mame:'白班'
          },{
            grade:'2',
            schedule:'夜班'
          }
        ],
        typeData:[
          {
            property:'3',
            mame:'请假'
          },{
            property:'2',
            mame:'加班'
          }
        ],

      };
    },
    mounted: function () {

    },
    methods: {

      onclick(){
        this.dialogTitle='护工参照';
        this.dialogVisible=true;
        this.getCareWorker();
      },
      SelectionChange(val){
        this.resourceSelection = val;
      },
      //查询陪护人员列表
      getCareWorker(){
        this.tableLoading = true;
        this.postRequestJson('/busi/careworker/list',{
          name: this.name,
          curPage: this.currentPage,
          projectId:2,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            const data = resp.data;
            this.careWorkerList =resp.data.msg.result;
            this.totalCount = data.msg.totalSize;
          }
        })
      },
      onClickEmp(){
       if(this.resourceSelection.length<0){
           this.getMasgg("请选择护工");
        }else{
            this.CareWorkName=this.resourceSelection.name;
            this.dialogVisible=false;
       }
      },

      subminOnclick(){
        let day=0;
        if(!(0<this.dayIndex<=1)){
           this.getMasgg("时间长度绝对值不能大于1");
           return;
        }
        if(this.property=3){
           day=0-this.dayIndex
        }
        this.postRequestJson('/busi/routine',{
          date: this.date,
          dayIndex:day,
          schedule:this.schedule,
          property:this.property,
          careworkerIds:[this.resourceSelection.careworkerId]
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMasgg(resp.data.msg);
          }
        })


      },


      //分页操作
      currentChange(currentChange){
        this.currentPage = currentChange;
        this.getCareWorker();
      },

      //消息提示
      getMasgg(data){
        this.$confirm(data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.dialogVisible=false;
        }).catch(() => {
        });
      },


    }
  };
</script>



