<template>

  <div style="margin-top: 15px;">

    <el-tabs type="card">
       <el-tab-pane label="科室管理" name="Organization" >
           <menu-org></menu-org>
       </el-tab-pane>

       <el-tab-pane label="陪护类型设置">
          <el-form>
                <el-row>
                    <el-col :span="6">
                       <el-form-item label="陪护类型名称">
                        <el-input prefix-icon="el-icon-edit"  v-model="ServiceName"  size="mini" style="width: 150px"  placeholder="请输入陪护类型名称"></el-input>
                       </el-form-item>
                    </el-col>
                  <el-col :span="6">
                    <el-form-item label="陪护属性:">
                      <el-select  v-model="property" size="mini"  placeholder="请选择陪护属性" style="width:150px">
                          <el-option  v-for="item in propertyData" :key="item.property" :label="item.mame" :value="item.property"> </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                      <el-form-item style="float: left">
                         <el-button size="mini"  icon="el-icon-search"  type="primary" @click="onServiceClick">查 询</el-button>
                     </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  style="float: right">
                        <el-button type="primary" size="mini" icon="el-icon-plus" @click="showAddEmpView">添加陪护类型 </el-button>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-table
                    :data="ServiceList"
                    v-loading="tableLoading"
                    border
                    size="mini"
                    style="width: 100%">
                    <el-table-column
                      prop="name"
                      align="center"
                      label="陪护类型名称">
                    </el-table-column>
                    <el-table-column
                      prop="id"
                      v-if="false"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="price"
                      align="center"
                      label="单价">
                    </el-table-column>
                    <el-table-column
                      prop="propertyName"
                      align="center"
                      label="陪护属性">
                    </el-table-column>
                    <el-table-column
                      prop="salaryIndex"
                      align="center"
                      label="工资系数(%)">
                    </el-table-column>
                    <el-table-column
                      prop="settleTypeName"
                      align="center"
                      label="计算方式">
                    </el-table-column>
                    <el-table-column
                      align="center"
                      label="操作"
                      width="150">
                      <template slot-scope="scope">
                        <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">编辑
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
                    <el-pagination
                      background
                      :page-size="10"
                      :current-page="currentPage"
                      @current-change="ServiceTiaChange"
                      layout="prev, pager, next"
                      :total="totalTaiCount">
                    </el-pagination>
                  </div>
                </el-row>
          </el-form>
          <!--添加陪护类型对话框-->
         <el-form :model="Servicetype" :rules="rules" style="margin: 0px;padding: 0px;">
           <div style="text-align: left">
             <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="ServicedialogVisible"  width="65%">
               <el-row>
                 <el-col :span="8">
                   <el-form-item label="陪护类型名称:" prop="name">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype.name"  size="mini" style="width: 150px"
                               placeholder="请输入类型名称"></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="陪护属性:">
                     <el-select  v-model="Servicetype.property" size="mini"  placeholder="请选择陪护属性" style="width:150px">
                       <el-option  v-for="item in propertyData" :key="item.property" :label="item.mame" :value="item.property"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="单 价:" prop="price">
                     <el-input prefix-icon="el-icon-edit"  v-model="Servicetype.price"   @keyup.native="number"  size="mini" style="width: 150px"
                               placeholder="请输入单价"></el-input>
                   </el-form-item>
                 </el-col>
               </el-row>
               <el-row>
                 <el-col :span="8">
                   <el-form-item label="工资系数:" prop="salaryIndex">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype.salaryIndex"   size="mini" style="width: 150px"  @keyup.native="number"
                               placeholder="请输入工资系数"></el-input>%
                   </el-form-item>
                 </el-col>

                 <el-col :span="8">
                   <el-form-item label="结算方式:" prop="phone">
                      <el-select  v-model="Servicetype.settleType" size="mini"  placeholder="请选择结算方式" style="width:150px">
                       <el-option  v-for="item in settleTypeData" :key="item.settleType" :label="item.mame" :value="item.settleType"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
               </el-row>
               <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="addServiceOnclick">确 定</el-button>
               </span>
             </el-dialog>
           </div>
         </el-form>
         <!--编辑陪护类型对话框-->
         <el-form :model="Servicetype2" :rules="rules"  style="margin: 0px;padding: 0px;">
           <div style="text-align: left">
             <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="MaydifydialogVisible"  width="65%">
               <el-row>
                 <el-col :span="8">
                   <el-form-item label="陪护类型名称:" prop="name">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype2.name"  size="mini" style="width: 150px"
                               placeholder="请输入类型名称"></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="陪护属性:">
                     <el-select  v-model="Servicetype2.property" size="mini"  placeholder="请选择陪护属性" style="width:150px">
                       <el-option  v-for="item in propertyData" :key="item.property" :label="item.mame" :value="item.property"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="单 价:" prop="price">
                     <el-input prefix-icon="el-icon-edit"  v-model="Servicetype2.price"   @keyup.native="number"  size="mini" style="width: 150px"
                               placeholder="请输入单价"></el-input>
                   </el-form-item>
                 </el-col>
               </el-row>
               <el-row>
                 <el-col :span="8">
                   <el-form-item label="工资系数:" prop="salaryIndex">
                     <el-input prefix-icon="el-icon-edit" v-model="Servicetype2.salaryIndex"  size="mini" style="width: 150px"  @keyup.native="number"
                               placeholder="请输入工资系数"></el-input>%
                   </el-form-item>
                 </el-col>

                 <el-col :span="8">
                   <el-form-item label="结算方式:" prop="phone">
                     <el-select  v-model="Servicetype2.settleType" size="mini"  placeholder="请选择结算方式" style="width:150px">
                       <el-option  v-for="item in settleTypeData" :key="item.settleType" :label="item.mame" :value="item.settleType"> </el-option>
                     </el-select>
                   </el-form-item>
                 </el-col>
               </el-row>
               <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="MadfiyServiceOnclick">确 定</el-button>
               </span>
             </el-dialog>
           </div>
         </el-form>
       </el-tab-pane>
       <el-tab-pane label="单据编号设置">
            <el-form v-model="docfile" :rules="rules" >
               <el-row>
                    <el-col :span="6">
                      <el-form-item label="单据名称" prop="CodeName">
                           <el-input prefix-icon="el-icon-edit"  v-model="docfile.CodeName"  size="mini" style="width: 150px"  placeholder="请输入陪护类型名称"></el-input>
                      </el-form-item>
                    </el-col>
                 <el-col :span="6">
                   <el-form-item label="起始单据号" prop="startCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile.startCode"  size="mini" style="width: 150px"  placeholder="请输入起始单据号"></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="6">
                   <el-form-item label="结束单据号" prop="endCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile.endCode"  size="mini" style="width: 150px"  placeholder="请输入结束单据号"></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="6">
                   <el-form-item style="float: left">
                     <el-button size="mini" icon="el-icon-plus"  type="primary" @click="onDocfileClick">添加单据号</el-button>
                   </el-form-item>
                   <el-form-item style="float: right">
                     <el-button size="mini" icon="el-icon-search"  type="primary" @click="getdocfileList">查 询</el-button>
                   </el-form-item>
                 </el-col>
               </el-row>
                <el-row>
                  <el-table
                    :data="docfileList"
                    v-loading="tableLoading"
                    border
                    size="mini"
                    style="width: 100%">
                    <el-table-column
                      prop="name"
                      align="center"
                      label="单据名称">
                    </el-table-column>
                    <el-table-column
                      prop="id"
                      v-if="false"
                      align="center">
                    </el-table-column>
                    <el-table-column
                      prop="currentCode"
                      align="center"
                      label="当前单据编号">
                    </el-table-column>
                    <el-table-column
                      prop="startCode"
                      align="center"
                      label="起始单据编号">
                    </el-table-column>
                    <el-table-column
                      prop="endCode"
                      align="center"
                      label="结束单据编号">
                    </el-table-column>
                    <el-table-column
                      align="center"
                      label="操作"
                      width="150">
                      <template slot-scope="scope">
                        <el-button @click="showDocfileView(scope.row)" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">编辑
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
                    <el-pagination
                      background
                      :page-size="10"
                      :current-page="currentPage"
                      @current-change="ServiceChange"
                      layout="prev, pager, next"
                      :total="totalCount">
                    </el-pagination>
                  </div>
                </el-row>
            </el-form>
             <el-form :model="docfile2"  style="margin: 0px;padding: 0px;">
           <div style="text-align: left">
             <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="docfileVisible"  width="65%">
               <el-row>
                 <el-col :span="8">
                   <el-form-item label="单据名称" >
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile2.name"  size="mini" style="width: 150px"  placeholder="请输入陪护类型名称"></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="起始单据号" prop="startCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile2.startCode"  size="mini" style="width: 150px"  placeholder="请输入起始单据号"></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="8">
                   <el-form-item label="结束单据号" prop="endCode">
                     <el-input prefix-icon="el-icon-edit"  v-model="docfile2.endCode"  size="mini" style="width: 150px"  placeholder="请输入结束单据号"></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span="14">
                   <el-form-item style="float: right">
                     <el-button size="mini" icon="el-icon-edit"  type="primary" @click="madyifdocfile">修改单据号</el-button>
                   </el-form-item>
                 </el-col>
               </el-row>
             </el-dialog>
           </div>
         </el-form>
       </el-tab-pane>
    </el-tabs>

  </div>


</template>

<script>
  import Organization from './basic/Organizationed'

  export default {
    data() {
      return {
        ServiceName:'',
        property:'',
        dialogTitle:'',
        docfileVisible:false,
        tableLoading:false,
        ServicedialogVisible:false,
        MaydifydialogVisible:false,
        ServiceList:[],
        docfileList:[],
        totalCount:-1,
        totalTaiCount:-1,
        currentPage: 1,
        Servicetype:{
          name:'',
          property:'',
          price:'',
          salaryIndex:'',
          settleType:'',
        },
        Servicetype2:{
          name:'',
          property:'',
          price:'',
          salaryIndex:'',
          settleType:'',
        },
        serviceId:'',
        docfile:{
          CodeName:'',
          startCode:'',
          endCode:'',
          currentCode:'',
        },
        docfile2:{
          name:'',
          startCode:'',
          currentCode:'',
          endCode:'',
          id:'',
        },


        propertyData:[
          {
            property:'1',
            mame:'一对一'
          },{
            property:'2',
            mame:'一对多'
          },{
            property:'3',
            mame:'多对多'
          }
        ],
        settleTypeData:[
          {
            settleType:'1',
            mame:'日'
          },{
            settleType:'2',
            mame:'例'
          }
        ],

        rules:{
          name: [{required: true, message: '必填:陪护类型名称', trigger: 'blur'}],
          price: [{required: true, message: '必填:单价', trigger: 'blur'}],
          salaryIndex: [{required: true, message: '必填:工资系数', trigger: 'blur'}],
          CodeName: [{required: true, message: '必填:单据名称', trigger: 'blur'}],
          startCode: [{required: true, message: '必填:起始单据号', trigger: 'blur'}],
          endCode: [{required: true, message: '必填:结束单据号', trigger: 'blur'}],
        },

      };
    },
    mounted: function () {
      this.onServiceClick();
      this.getdocfileList();
    },

    methods: {
      number(){
        this.Servicetype.salaryIndex=this.Servicetype.salaryIndex.replace(/[^\.\d]/g,'');
        this.Servicetype2.salaryIndex=this.Servicetype.salaryIndex.replace(/[^\.\d]/g,'');
        this.Servicetype.price=this.Servicetype.price.replace(/[^\.\d]/g,'');
        this.Servicetype2.price=this.Servicetype.price.replace(/[^\.\d]/g,'');
      },
      //查询陪护类型列表
      onServiceClick(){
        this.tableLoading=true;
        this.postRequestJson('/busi/baseinfo/caretype/list',{
          name:this.ServiceName,
          property:this.property,
          curPage: this.currentPage,
          projectId:this.user.projectId,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              this.ServiceList =resp.data.msg.result;
              this.totalTaiCount = resp.data.msg.totalSize;
            }else{
              this.getMssage(resp.data.msg);
            }
          }else{
            this.getMssage(resp.status);
          }
        })
      },

      //分页
      ServiceChange(ServiceChange){
        this.currentPage = ServiceChange;
        this.getdocfileList();
      },
      ServiceTiaChange(ServiceTiaChange){
        this.currentPage = ServiceTiaChange;
        this.onServiceClick();
      },
      //弹出添加陪护类型对话框
      showAddEmpView(){
        this.dialogTitle='添加陪护类型';
        this.ServicedialogVisible=true;
        this.Servicetype.price='';
        this.Servicetype.salaryIndex='';
        this.Servicetype.settleType='';
        this.Servicetype.name='';
        this.Servicetype.property='';
      },

      //添加陪护类型操作
      addServiceOnclick(){
        this.postRequestJson('/busi/baseinfo/caretype',{
          name:this.Servicetype.name,
          property:this.Servicetype.property,
          price:this.Servicetype.price,
          salaryIndex:this.Servicetype.salaryIndex,
          settleType:this.Servicetype.settleType,
          projectId:this.user.projectId,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.status);
          }
        })
      },
       //修改陪护类型
      MadfiyServiceOnclick(){
        this.putRequestJson('/busi/baseinfo/caretype',{
          id:this.serviceId,
          name:this.Servicetype2.name,
          property:this.Servicetype2.property,
          price:this.Servicetype2.price,
          salaryIndex:this.Servicetype2.salaryIndex,
          settleType:this.Servicetype2.settleType,
          projectId:this.user.projectId,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.status);
          }
        })

      },

      //弹出编辑陪护类型对话框
      showEditEmpView(row){
        this.dialogTitle='编辑陪护类型';
        this.MaydifydialogVisible=true;
        this.Servicetype2.price=row.price;
        this.Servicetype2.name=row.name;
        this.serviceId=row.id;
        this.Servicetype2.salaryIndex=row.salaryIndex
      },
      //取消按钮
      cancelEidt(){
        this.ServicedialogVisible=false;
        this.MaydifydialogVisible=false;
      },
      //消息提示框
      getMssage(data){
        this.$confirm( data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.ServicedialogVisible=false;
          this.MaydifydialogVisible=false;
          this.docfileVisible=false;
          this.onServiceClick();
          this.getdocfileList();
        }).catch(() => {
        });
      },

      //查询单据列表
      getdocfileList(){
        this.tableLoading = true;
        this.postRequestJson('/busi/baseinfo/docfile/list',{
          name:'',
          curPage: this.currentPage,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code==="200"){
              this.docfileList =resp.data.msg.result;
              this.totalCount = resp.data.msg.totalSize;
            }else{
              this.getMssage(resp.data.msg);
            }
          }else{
            this.getMssage(resp.status);
          }
        })
      },
       //添加单据
      onDocfileClick(){
        this.postRequestJson('/busi/baseinfo/docfile',{
          name:this.docfile.CodeName,
          startCode:this.docfile.startCode,
          endCode:this.docfile.endCode,
          projectId:this.user.projectId,
        }).then(resp=> {
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.data.msg);
          }
        })
      },
      //打开编辑单据对话框
      showDocfileView(row){
        this.dialogTitle='编辑单据编号';
        this.docfileVisible=true;
        this.docfile2.id=row.id;
        this.docfile2.name=row.name;
        this.docfile2.startCode=row.startCode;
        this.docfile2.endCode=row.endCode;
      },
      //修改单据
      madyifdocfile(){
        this.putRequestJson('/busi/baseinfo/docfile',{
          name:this.docfile2.name,
          startCode:this.docfile2.startCode,
          currentCode:this.docfile2.startCode,
          endCode:this.docfile2.endCode,
          id:this.docfile2.id,
          projectId:this.user.projectId,
        }).then(resp=> {
          if (resp && resp.status === 200) {
            this.getMssage(resp.data.msg);
          }else{
            this.getMssage(resp.data.msg);
          }
        })
      },

    },
    components: {
      'menu-org': Organization,
    },
    computed: {
    user() {
      return this.$store.state.user;
    },
  }


  };
</script>

<style scoped>

</style>



