<template>
  <el-form  ref="OrgForm" style="margin: 0px;padding: 0px;">
    <div style="text-align: left">
      <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="EdialogVisible"  width="30%">
        <el-row>
          <el-tree
            :props="defaultProps"
            :load="loadChild"
            node-key="id"
            lazy
            show-checkbox
            ref="tree"
            @check="orgNodeClick"
          >
          </el-tree>
        </el-row>
        <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="">确 定</el-button>
               </span>
      </el-dialog>
    </div>
  </el-form>
</template>

<script>
export default {
  data () {
    return {
      name: 'OrgTree',
      treeProps: {
        children: 'children',
        label: 'label',
        id: 'id',
        isLeaf: 'isLeaf',
        disabled: 'disabled'
      }
    }
  },

  method: {
    orgNodeClick (data, nodes) {
      console.log(data)
      console.log(nodes)
      let target = []
      target.push(data)
      console.log(this)
      this.$refs.tree.setCheckedNodes(target, true)
    },

    submitClick () {

    }
  }
}
</script>

<style scoped>

