<template>
  <div>
    <div>
      <el-tree
        :props="defaultProps"
        :load="loadChild"
        node-key="id"
        lazy
        ref="tree"
        :filter-node-method="filterNode"
        v-loading="treeLoading"
        style="width: 500px;margin-top: 10px"
        :render-content="renderContent">
      </el-tree>

      <div style="text-align: left">
        <el-dialog title="添加科室" :visible.sync="dialogVisible" width="50%">
          <el-form>
            <el-row>
              <el-col :span="8">
                <el-form-item label="科室编号:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData.code" size="mini" style="width: 130px"
                            placeholder="请输入类型名称"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室名称:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData.name" size="mini" style="width: 130px"
                            placeholder="请输入科室名称"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室负责人:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData.principal" size="mini" style="width: 118px"
                            placeholder="请输入科室负责人"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <el-form-item label="上级单位:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData.faName" size="mini" style="width: 130px"
                            disabled="disabled"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="联系方式:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData.phone" size="mini" style="width: 130px"
                            placeholder="请输入联系方式"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室地址:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData.address" size="mini" style="width: 130px"
                            placeholder="请输入科室地址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
          <span slot="footer" class="dialog-footer">
                  <el-button size="small" @click="dialogVisible = false">取消</el-button>
                  <el-button size="small" type="primary" @click="addDep">添加</el-button>
          </span>
        </el-dialog>

        <el-dialog title="编辑科室" :visible.sync="orgDialogVisible" width="50%">
          <el-form>
            <el-row>
              <el-col :span="8">
                <el-form-item label="科室编号:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData2.code" size="mini" style="width: 130px"
                            placeholder="请输入类型名称"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室名称:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData2.name" size="mini" style="width: 130px"
                            placeholder="请输入科室名称"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室负责人:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData2.principal" size="mini" style="width: 118px"
                            placeholder="请输入科室负责人"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <el-form-item label="联系方式:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData2.phone" size="mini" style="width: 130px"
                            placeholder="请输入联系方式"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室地址:">
                  <el-input prefix-icon="el-icon-edit" v-model="orgData2.address" size="mini" style="width: 130px"
                            placeholder="请输入科室地址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
          <span slot="footer" class="dialog-footer">
                  <el-button size="small" @click="orgDialogVisible = false">取消</el-button>
                  <el-button size="small" type="primary" @click="UpdateDep">修改</el-button>
          </span>
        </el-dialog>

        <el-dialog title="科室信息" :visible.sync="orgVisible" width="50%">
          <el-form>
            <el-row>
              <el-col :span="8">
                <el-form-item label="科室编号:">
                  {{orgData2.code}}
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室名称:">
                  {{orgData2.name}}
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室负责人:">
                  {{orgData2.principal}}
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <el-form-item label="联系方式:">
                  {{orgData2.phone}}
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="科室地址:">
                  {{orgData2.address}}
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
          <span slot="footer" class="dialog-footer">
                  <el-button size="small" @click="orgVisible = false">关闭</el-button>
          </span>
        </el-dialog>

      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    const data = [
      {
        children: [],
        label: '项目',
        id: 0,
        isLeaf: false
      }
    ]
    return {
      treeInitData: JSON.parse(JSON.stringify(data)),
      depName: '',
      treeLoading: false,
      dialogVisible: false,
      orgDialogVisible: false,
      orgVisible: false,
      allDeps: [],
      pDep: '',
      treeData: [],
      defaultProps: {
        children: 'children',
        label: 'label',
        id: 'id',
        isLeaf: 'isLeaf',
        disabled: 'disabled'
      },
      orgDataNode: {
        orgId: '',
        orgName: ''
      },
      orgData: {
        faId: '',
        faName: '',
        name: '',
        address: '',
        principal: '',
        phone: '',
        code: ''
      },
      orgData2: {
        id: '',
        faId: '',
        faName: '',
        name: '',
        address: '',
        principal: '',
        phone: '',
        code: ''
      }

    }
  },
  mounted: function () {

  },
  methods: {
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    // 加载树形
    loadChild (node, resolve) {
      if (node.parent == null) {
        const data = []
        data.push({
          children: [],
          label: '项目',
          id: null,
          isLeaf: false,
          disabled: true
        })
        resolve(data)
        return
      }
      let id = node.data.id
      let resolve1 = resolve
      this.postRequestJson('/busi/baseinfo/organization/list', {
        orgId: id,
        projectId: this.user.projectId
      }).then(resp => {
        this.tableLoading = false
        if (resp && resp.status === 200) {
          this.treeLoading = false
          const tdata = []
          for (let i = 0; i < resp.data.msg.result.length; i++) {
            tdata.push({
              id: resp.data.msg.result[i].id,
              label: resp.data.msg.result[i].name,
              children: [],
              isLeaf: resp.data.msg.result[i].isLeaf,
              disabled: resp.data.msg.result[i].isDisabled
            })
          }
          resolve1(tdata)
        }
      })
    },

    addDep () {
      var _this = this
      this.dialogVisible = false
      this.treeLoading = true
      this.postRequestJson('/busi/baseinfo/organization', {
        name: this.orgData.name,
        code: this.orgData.code,
        faId: this.pDep,
        address: this.orgData.address,
        principal: this.orgData.principal,
        phone: this.orgData.phone,
        projectId: this.user.projectId
      }).then(resp => {
        _this.treeLoading = false
        if (resp && resp.status === 200) {
          this.getMssage(resp.data.msg)
          this.getRewOrgcod(this.pDep)
        }
      })
    },
    UpdateDep () {
      this.putRequestJson('/busi/baseinfo/organization', {
        name: this.orgData2.name,
        code: this.orgData2.code,
        id: this.pDep,
        address: this.orgData2.address,
        principal: this.orgData2.principal,
        phone: this.orgData2.phone,
        projectId: this.user.projectId
      }).then(resp => {
        if (resp && resp.status === 200) {
          this.getMssage(resp.data.msg)
          this.orgDialogVisible = false
        }
      })
    },

    // 消息提示框
    getMssage (data) {
      this.$confirm(data, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.dialogVisible = false
      }).catch(() => {
      })
    },

    // 打开添加科室对话框
    showAddDepView (data, event) {
      this.dialogVisible = true
      this.pDep = data.id
      this.orgData.faName = data.label
      event.stopPropagation()
    },
    // 打开编辑科室对话框
    showUpdateDep (data, event) {
      this.orgDialogVisible = true
      this.pDep = data.id
      this.orgData2.faName = data.label
      this.getRequest('/busi/baseinfo/organization?id=' + data.id).then(resp => {
        if (resp && resp.status === 200) {
          if (resp.data.code = 200) {
            this.orgData2.code = resp.data.msg.code
            this.orgData2.name = resp.data.msg.name
            this.orgData2.phone = resp.data.msg.phone
            this.orgData2.principal = resp.data.msg.principal
            this.orgData2.address = resp.data.msg.address
          } else {
            this.getMssage(resp.data.msg)
          }
        }
      })
      event.stopPropagation()
    },
    // 打开科室信息对话框
    orgVisibleDep (data, event) {
      this.orgVisible = true
      this.pDep = data.id
      this.orgData2.faName = data.label
      this.getRequest('/busi/baseinfo/organization?id=' + data.id).then(resp => {
        if (resp && resp.status === 200) {
          if (resp.data.code = 200) {
            this.orgData2.code = resp.data.msg.code
            this.orgData2.name = resp.data.msg.name
            this.orgData2.phone = resp.data.msg.phone
            this.orgData2.principal = resp.data.msg.principal
            this.orgData2.address = resp.data.msg.address
          } else {
            this.getMssage(resp.data.msg)
          }
        }
      })
      event.stopPropagation()
    },

    deleteDep (data, event, node, strYn) {
      var _this = this
      let key = node.parent.key
      if (data.children.length > 0) {
        this.$message({
          message: '该科室下尚有其他部门，不能被停用!',
          type: 'warning'
        })
      } else {
        this.$confirm('停用[' + data.label + ']科室, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.treeLoading = true
          _this.deleteRequest('/busi/baseinfo/organization?id=' + data.id + '&status=' + strYn).then(resp => {
            _this.treeLoading = false
            if (resp && resp.status === 200) {
              this.getMssage(resp.data.msg)
            }
            this.getRewOrgcod(key)
          })
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
      event.stopPropagation()
    },

    getRewOrgcod (id) {
      this.postRequestJson('/busi/baseinfo/organization/list', {
        orgId: id,
        projectId: this.user.projectId
      }).then(resp => {
        this.tableLoading = false
        if (resp && resp.status === 200) {
          this.treeLoading = false
          const tdata = []
          for (let i = 0; i < resp.data.msg.result.length; i++) {
            tdata.push({
              id: resp.data.msg.result[i].id,
              label: resp.data.msg.result[i].name,
              children: [],
              isLeaf: resp.data.msg.result[i].isLeaf,
              disabled: resp.data.msg.result[i].isDisabled
            })
          }
          this.$refs.tree.updateKeyChildren(id, tdata)
        }
      })
    },

    renderContent (h, {node, data, store}) {
      let str1 = (
        <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
          <span>
            <span>{node.label}</span>
          </span>
          <span>
            <el-button style="font-size: 12px;" type="primary" icon="el-icon-plus" round size="mini"
              style="padding:3px"
              on-click={() => this.showAddDepView(data, event)}></el-button>
            <el-button style="font-size: 12px;" type="primary" round icon="el-icon-edit" size="mini"
              style="padding:3px"
              on-click={() => this.showUpdateDep(data, event)}></el-button>
            <el-button style="font-size: 12px;" type="primary" round icon="el-icon-info" size="mini" style="padding:3px"
              on-click={() => this.orgVisibleDep(data, event)}></el-button>

            <el-button style="font-size: 12px;" type="primary" round size="mini" style="padding:3px">启用科室</el-button>
            <el-button on-click={() => this.deleteDep(data, event, node, false)} style="font-size: 12px;" type="danger" round
              size="mini" style="padding:3px">停用科室</el-button>
          </span>
        </span>
      )

      let str2 = (
        <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
          <span>
            <span>{node.label}</span>
          </span>
          <span>
            <el-button style="font-size: 12px;" type="primary" icon="el-icon-plus" round size="mini"
              style="padding:3px"
              on-click={() => this.showAddDepView(data, event)}></el-button>
            <el-button style="font-size: 12px;" type="primary" round icon="el-icon-edit" size="mini"
              style="padding:3px"
              on-click={() => this.showUpdateDep(data, event)}></el-button>
            <el-button style="font-size: 12px;" type="primary" round icon="el-icon-info" size="mini" style="padding:3px"
              on-click={() => this.orgVisibleDep(data, event)}></el-button>
          </span>
        </span>
      )
      let str3 = (
        <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
          <span>
            <span>{node.label}</span>
          </span>
          <span>
            <el-button style="font-size: 12px;" type="primary" icon="el-icon-plus" round size="mini"
              style="padding:3px"
              on-click={() => this.showAddDepView(data, event)}></el-button>
            <el-button style="font-size: 12px;" type="primary" round icon="el-icon-edit" size="mini"
              style="padding:3px"
              on-click={() => this.showUpdateDep(data, event)}></el-button>
            <el-button style="font-size: 12px;" type="primary" round icon="el-icon-info" size="mini" style="padding:3px"
              on-click={() => this.orgVisibleDep(data, event)}></el-button>

            <el-button on-click={() => this.deleteDep(data, event, node, true)} style="font-size: 12px;" type="danger" round
              size="mini" style="padding:3px">停用科室</el-button>
          </span>
        </span>
      )
      let str4 = (
        <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
          <span>
            <span>{node.label}</span>
          </span>
          <span>
            <el-button style="font-size: 12px;" type="primary" round size="mini" style="padding:3px" on-click={() => this.deleteDep(data, event, node, false)}>启用科室</el-button>
          </span>
        </span>
      )

      let str = ''
      if (!node.isLeaf) {
        str = str2
      } else {
        if (!node.disabled) {
          str = str3
        } else {
          str = str4
        }
      }
      return str
    }
  },
  computed: {
    user () {
      return this.$store.state.user
    }
  }

}
</script>



