                <div style="float:left;height:260px;width:16px;font-size:11px;text-align:right;">
                  <a style="width:5px;word-wrap:break-word;word-spacing:3px;position:relative;top:30px;">①存根︵ 白︶</a>
                  <a style="width:5px;word-wrap:break-word;word-spacing:3px;position:relative;top:40px;">②收据︵红︶</a>
