    </el-form>

    <!-- 科室对照界面-->
    <el-form ref="OrgForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog :title="orgdialogTitle" style="padding: 0px;" :close-on-click-modal="false"
                   :visible.sync="EdialogVisible" width="30%">
          <el-row>
            <el-tree
              :props="defaultProps"
