    <!-- 陪护类型对照界面-->
    <el-form ref="ServiceForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog :title="SdialogTitle" style="padding: 0px;" :close-on-click-modal="false"
                   :visible.sync="SdialogVisible" width="44%">
          <el-row>
            <el-table
              :data="CareTypeList"
              v-loading="tableLoading"
              border
              highlight-current-row
              @current-change="SelectionChange"
              size="mini"
              style="width: 100%">
              <!-- <el-table-column
                 type="selection"
                 align="left"
                 width="30">
               </el-table-column>-->
              <el-table-column
                prop=""
                v-if="hideRow"
                label="陪护人员记录号"
